"use client";

import React, { useEffect, useState, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/hooks/use-auth';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Tabs, TabsList, TabsTrigger, TabsContent } from '@/components/ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Input } from '@/components/ui/input';
import { Separator } from '@/components/ui/separator';
import { Skeleton } from '@/components/ui/skeleton';
import { toast } from '@/components/ui/use-toast';
import { Search, Calendar, BarChart4, Clock } from 'lucide-react';
import { format } from 'date-fns';
import ResultsTable from './components/ResultsTable';
import ScoreChart from './components/ScoreChart';
import SubjectAnalysis from './components/SubjectAnalysis';
import ResultsHeader from './components/ResultsHeader';

interface QuizResultItem {
  attemptId: string;
  quizId: string;
  quizTitle: string;
  quizDescription: string | null;
  className: string | null;
  category: string | null;
  score: number;
  totalQuestions: number;
  correctQuestions: number;
  completedAt: string;
  timeTakenMinutes: number | null;
  feedbackAvailable: boolean;
}

interface ResultsData {
  results: QuizResultItem[];
  pagination: {
    currentPage: number;
    totalPages: number;
    totalItems: number;
    pageSize: number;
  };
  timeFrame: string;
  subject: string | null;
}

interface SubjectPerformance {
  name: string;
  score: number;
  quizCount: number;
  improvement: number | null;
}

// Default stats values
const defaultOverallStats = {
  averageScore: 0,
  previousAverageScore: null,
  improvement: null,
  totalAssessments: 0,
  currentPeriodLabel: 'last 30 days',
  previousPeriodLabel: null
};

const defaultCompletionData = {
  totalQuizzes: 0,
  completedQuizzes: 0,
  completionRate: 0,
  bestSubject: {
    name: '',
    score: 0
  },
  mostImprovedSubject: null
};

// Generate time series data for ScoreChart
const generateTimeSeriesData = (results: QuizResultItem[]) => {
  if (!results.length) return [];

  // Group by date
  const dateMap = new Map<string, {scores: number[], count: number}>();
  
  results.forEach(result => {
    // Extract date part only from ISO string
    const date = result.completedAt.split('T')[0];
    if (!dateMap.has(date)) {
      dateMap.set(date, {scores: [], count: 0});
    }
    
    const dateData = dateMap.get(date)!;
    dateData.scores.push(result.score);
    dateData.count++;
  });
  
  // Convert to array and calculate averages
  return Array.from(dateMap.entries())
    .map(([date, data]) => {
      const avgScore = data.scores.reduce((sum, score) => sum + score, 0) / data.scores.length;
      return {
        date,
        averageScore: avgScore,
        assessmentCount: data.count
      };
    })
    .sort((a, b) => a.date.localeCompare(b.date));
};

// Convert our subject performance data to match SubjectAnalysis component
const convertToSubjectPerformance = (subjects: SubjectPerformance[]): {
  subject: string;
  averageScore: number;
  assessmentCount: number;
  trend: number | null;
}[] => {
  return subjects.map(subj => ({
    subject: subj.name,
    averageScore: subj.score,
    assessmentCount: subj.quizCount,
    trend: subj.improvement
  }));
};

export default function StudentResultsPage() {
  const { isStudent, isLoading: authLoading } = useAuth();
  const router = useRouter();

  // State management
  const [timeFrame, setTimeFrame] = useState<string>('last30days');
  const [subject, setSubject] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [pageSize] = useState<number>(10);
  
  // Data state
  const [resultsData, setResultsData] = useState<ResultsData | null>(null);
  const [filteredResults, setFilteredResults] = useState<QuizResultItem[]>([]);
  const [subjects, setSubjects] = useState<string[]>([]);
  const [overallStats, setOverallStats] = useState(defaultOverallStats);
  const [completionData, setCompletionData] = useState(defaultCompletionData);
  const [subjectsPerformance, setSubjectsPerformance] = useState<SubjectPerformance[]>([]);
  
  // UI state
  const [loading, setLoading] = useState<boolean>(true);
  const [tab, setTab] = useState<string>('all');

  // Fetch results data
  const fetchResults = useCallback(async () => {
    setLoading(true);
    try {
      const response = await fetch(
        `/api/users/me/quiz-results?timeFrame=${timeFrame}${subject ? `&subject=${subject}` : ''}&page=${currentPage}&pageSize=${pageSize}`
      );
      
      if (!response.ok) {
        throw new Error(`Error ${response.status}: ${response.statusText}`);
      }
      
      const data = await response.json();
      
      if (!data.success) {
        throw new Error(data.message || 'Failed to load results');
      }
      
      setResultsData(data.data);
      setFilteredResults(data.data.results);
      
      // Extract unique subjects
      const allSubjects = data.data.results
        .map((result: QuizResultItem) => result.category)
        .filter((category: string | null) => category !== null) as string[];
      setSubjects([...new Set(allSubjects)]);
      
      // Calculate stats
      if (data.data.results.length > 0) {
        // Overall stats
        const scores = data.data.results.map((r: QuizResultItem) => r.score);
        const avgScore = scores.reduce((acc: number, score: number) => acc + score, 0) / scores.length;
        
        setOverallStats({
          averageScore: avgScore,
          previousAverageScore: null, // Would come from a separate API call typically
          improvement: null, // Would be calculated from previous period
          totalAssessments: data.data.pagination.totalItems,
          currentPeriodLabel: formatTimeFrameLabel(timeFrame),
          previousPeriodLabel: null
        });
        
        // Completion data
        const completedCount = data.data.results.length;
        const totalCount = data.data.pagination.totalItems;
        
        // Group by subject
        const subjectMap = new Map<string, { scores: number[], count: number }>();
        data.data.results.forEach((result: QuizResultItem) => {
          const subjectName = result.category || 'Uncategorized';
          if (!subjectMap.has(subjectName)) {
            subjectMap.set(subjectName, { scores: [], count: 0 });
          }
          const subjectData = subjectMap.get(subjectName)!;
          subjectData.scores.push(result.score);
          subjectData.count++;
        });
        
        // Find best subject
        let bestSubject = { name: '', score: 0 };
        const subjectPerformanceList: SubjectPerformance[] = [];
        
        subjectMap.forEach((data, name) => {
          const avgScore = data.scores.reduce((acc, score) => acc + score, 0) / data.scores.length;
          
          subjectPerformanceList.push({
            name,
            score: avgScore,
            quizCount: data.count,
            improvement: null // Would come from historical data
          });
          
          if (avgScore > bestSubject.score) {
            bestSubject = { name, score: avgScore };
          }
        });
        
        setSubjectsPerformance(subjectPerformanceList);
        
        setCompletionData({
          totalQuizzes: totalCount,
          completedQuizzes: completedCount,
          completionRate: totalCount > 0 ? (completedCount / totalCount) * 100 : 0,
          bestSubject,
          mostImprovedSubject: null // Would be calculated from historical data
        });
      }
    } catch (error) {
      console.error('Error fetching results:', error);
      toast({
        title: 'Error',
        description: error instanceof Error ? error.message : 'Failed to load results',
        variant: 'destructive',
      });
    } finally {
      setLoading(false);
    }
  }, [timeFrame, subject, currentPage, pageSize]);

  // Effect to handle filtering based on search term
  useEffect(() => {
    if (!resultsData) return;
    
    const filtered = resultsData.results.filter(result => {
      return !searchTerm 
        || result.quizTitle.toLowerCase().includes(searchTerm.toLowerCase()) 
        || (result.className?.toLowerCase().includes(searchTerm.toLowerCase()))
        || (result.category?.toLowerCase().includes(searchTerm.toLowerCase()));
    });
    
    setFilteredResults(filtered);
  }, [searchTerm, resultsData]);

  // Initial data fetch
  useEffect(() => {
    if (!authLoading && isStudent) {
      fetchResults();
    } else if (!authLoading && !isStudent) {
      router.push('/dashboard');
    }
  }, [authLoading, isStudent, fetchResults, router]);
  
  // Handle time frame change
  const handleTimeFrameChange = (value: string) => {
    setTimeFrame(value);
    setCurrentPage(1);
  };
  
  // Handle subject change
  const handleSubjectChange = (value: string) => {
    setSubject(value === 'all' ? null : value);
    setCurrentPage(1);
  };
  
  // Format time frame for display
  const formatTimeFrameLabel = (timeFrame: string): string => {
    switch (timeFrame) {
      case 'last7days':
        return 'last 7 days';
      case 'last30days':
        return 'last 30 days';
      case 'last90days':
        return 'last 90 days';
      case 'last6months':
        return 'last 6 months';
      case 'lastYear':
        return 'last year';
      case 'allTime':
        return 'all time';
      default:
        return timeFrame;
    }
  };
  
  if (authLoading) {
    return <ResultsPageSkeleton />;
  }
  
  return (
    <div className="container py-6 space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">My Results</h1>
          <p className="text-muted-foreground">
            View and analyze your assessment results
          </p>
        </div>
      </div>
      
      <div className="flex flex-col md:flex-row gap-4 md:items-center md:justify-between">
        <div className="flex flex-col sm:flex-row gap-4">
          <div>
            <label className="text-sm font-medium" htmlFor="time-frame-select">
              Time Period
            </label>
            <Select 
              value={timeFrame} 
              onValueChange={handleTimeFrameChange}
            >
              <SelectTrigger className="w-[180px]" id="time-frame-select">
                <SelectValue placeholder="Select period" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="last7days">Last 7 Days</SelectItem>
                <SelectItem value="last30days">Last 30 Days</SelectItem>
                <SelectItem value="last90days">Last 90 Days</SelectItem>
                <SelectItem value="last6months">Last 6 Months</SelectItem>
                <SelectItem value="lastYear">Last Year</SelectItem>
                <SelectItem value="allTime">All Time</SelectItem>
              </SelectContent>
            </Select>
          </div>
          
          {subjects.length > 0 && (
            <div>
              <label className="text-sm font-medium" htmlFor="subject-select">
                Subject
              </label>
              <Select 
                value={subject || 'all'} 
                onValueChange={handleSubjectChange}
              >
                <SelectTrigger className="w-[180px]" id="subject-select">
                  <SelectValue placeholder="All subjects" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Subjects</SelectItem>
                  {subjects.map(subj => (
                    <SelectItem key={subj} value={subj}>{subj}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          )}
        </div>
        
        <div className="relative w-full md:w-auto">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
          <Input
            className="pl-10 w-full md:w-[280px]"
            placeholder="Search assessments..." 
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
      </div>
      
      <ResultsHeader 
        overallStats={overallStats} 
        completionData={completionData} 
      />
      
      <Tabs value={tab} onValueChange={setTab}>
        <TabsList className="mb-4">
          <TabsTrigger value="all">
            All Results
          </TabsTrigger>
          <TabsTrigger value="trends">
            Score Trends
          </TabsTrigger>
          <TabsTrigger value="subjects">
            Subject Analysis
          </TabsTrigger>
        </TabsList>
        
        <TabsContent value="all">
          {loading ? (
            <TableSkeleton />
          ) : (
            <ResultsTable 
              results={filteredResults}
              selectedTimeFrame={timeFrame}
              selectedSubject={subject}
              isLoading={loading}
            />
          )}
          
          {/* Pagination */}
          {resultsData?.pagination && resultsData.pagination.totalPages > 1 && (
            <div className="flex justify-center mt-6">
              <nav className="flex space-x-2" aria-label="Pagination">
                {/* Previous button */}
                <Button
                  variant="outline" 
                  size="sm"
                  disabled={currentPage === 1 || loading}
                  onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                >
                  Previous
                </Button>
                
                {/* Page numbers */}
                {Array.from({ length: resultsData.pagination.totalPages }, (_, i) => i + 1)
                  .filter(page => 
                    page === 1 ||
                    page === resultsData.pagination.totalPages ||
                    (page >= currentPage - 1 && page <= currentPage + 1)
                  )
                  .map((page, index, array) => {
                    // Add ellipsis
                    if (index > 0 && page > array[index - 1] + 1) {
                      return (
                        <span 
                          key={`ellipsis-${page}`} 
                          className="flex h-9 w-9 items-center justify-center"
                        >
                          ...
                        </span>
                      );
                    }
                    
                    return (
                      <Button
                        key={page}
                        variant={currentPage === page ? "default" : "outline"}
                        size="sm"
                        onClick={() => setCurrentPage(page)}
                        disabled={loading}
                      >
                        {page}
                      </Button>
                    );
                  })}
                
                {/* Next button */}
                <Button
                  variant="outline"
                  size="sm"
                  disabled={currentPage === resultsData.pagination.totalPages || loading}
                  onClick={() => setCurrentPage(p => Math.min(resultsData.pagination.totalPages, p + 1))}
                >
                  Next
                </Button>
              </nav>
            </div>
          )}
        </TabsContent>
        
        <TabsContent value="trends">
          <Card className="col-span-4">
            <CardHeader>
              <CardTitle>Score Trends</CardTitle>
              <CardDescription>Your performance over time</CardDescription>
            </CardHeader>
            <CardContent className="h-[400px]">
              {loading ? (
                <div className="w-full h-full flex items-center justify-center">
                  <Skeleton className="w-full h-full" />
                </div>
              ) : (
                <ScoreChart 
                  timeSeriesData={generateTimeSeriesData(resultsData?.results || [])} 
                  timeFrame={timeFrame}
                />
              )}
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="subjects">
          {loading ? (
            <div className="grid gap-4 grid-cols-1 lg:grid-cols-3">
              <Skeleton className="h-[300px]" />
              <Skeleton className="h-[300px]" />
              <Skeleton className="h-[300px]" />
            </div>
          ) : (
            <SubjectAnalysis
              subjectPerformance={convertToSubjectPerformance(subjectsPerformance)}
              selectedSubject={subject || undefined}
            />
          )}
        </TabsContent>
      </Tabs>
    </div>
  );
}

// Skeleton loaders
function ResultsPageSkeleton() {
  return (
    <div className="container py-6 space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <Skeleton className="h-8 w-64 mb-2" />
          <Skeleton className="h-4 w-48" />
        </div>
      </div>
      
      <div className="flex gap-4">
        <Skeleton className="h-10 w-40" />
        <Skeleton className="h-10 w-40" />
      </div>
      
      <div className="grid gap-4 md:grid-cols-4">
        <Skeleton className="h-32" />
        <Skeleton className="h-32" />
        <Skeleton className="h-32" />
        <Skeleton className="h-32" />
      </div>
      
      <Skeleton className="h-10 w-64" />
      
      <TableSkeleton />
    </div>
  );
}

function TableSkeleton() {
  return (
    <Card>
      <CardHeader>
        <Skeleton className="h-6 w-48" />
        <Skeleton className="h-4 w-32" />
      </CardHeader>
      <CardContent className="space-y-4">
        <Skeleton className="h-10 w-full" />
        <div className="space-y-2">
          {Array.from({ length: 5 }).map((_, i) => (
            <Skeleton key={i} className="h-16 w-full" />
          ))}
        </div>
      </CardContent>
    </Card>
  );
} 